<!-- 博客主页面 -->

<template>
    <el-row>
        <el-col v-for='(blog, index) in form' :key="index">
            <el-card :body-style="{ padding: '20px' }" >
                <div style="padding: 14px;" @click="push(blog)">
                    <h1>{{ blog.name }}</h1><br>
                    <span>{{ blog.userName }}</span><br>
                </div>
            </el-card>
        </el-col>
    </el-row>
</template>

<script>
import axios from 'axios';
const options = {
    data() {
        return {
            form: {

            }
        }
    },
    methods: {
        push(blog) {
            this.$store.commit('saveBlog', blog);
            //跳转到VideoOne界面
            this.$router.push('/BlogOne')
        },
        selectBlog() {
            axios.request({
                baseURL: "http://localhost:8090/",
                url: "/blog/select",
                method: 'post',
                headers: { 'Content-Type': 'application/json', 'X-Requested-With': 'XMLHttpRequest' }
            })
                .then(res => {
                    this.form = res.data;
                });
        }
    },
    mounted: function () {
        this.selectBlog();
    }
}
export default options;

</script>